<template>
  <div>
    <header>
      CGRSXYX
    </header>
    <div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>你觉得这是一个普通的网站吗？</p>
      </div>
      <div ref="text" :style="{ height: windowHeight + 'px' }" class="common">
        <p>emmmmm... 确实，它很普通</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>就像你划到了现在</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>却发现它只有这一个页面</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>继续向下划</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p class="rainbow-text">这条颜色是彩色的!</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <div class="question">
          <div><p>现在还觉得它普通吗？</p></div>
          <div>
            <el-button type="info" round @click="open">评价</el-button>
          </div>
        </div>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>别划了，最后一页了！</p>
      </div>
      <div :style="{ height: windowHeight + 'px' }" class="common">
        <p>逗你的，其实这才是最后一页</p>
      </div>
      <footer>
        Copyright 2024 Designed by Hsinyuwang
      </footer>
    </div>
  </div>
</template>

<script>
import gsap from 'gsap';

export default {
  name: 'HomeView',
  data() {
    return {
      windowHeight: window.innerHeight // 初始化窗口高度
    };
  },
  methods: {
    updateWindowHeight() {
      this.windowHeight = window.innerHeight; // 更新窗口高度
    },
    open() {
      this.$confirm('现在还觉得它普通吗?', '提示', {
        confirmButtonText: '真不错',
        cancelButtonText: '什么玩意',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '哎！这就对了'
        });

      }).catch(() => {
        this.$message.error('想好了再选！');
      });
    },
    animateText(element, index) {
      const animations = [
        { opacity: 0, y: 50, duration: 1, ease: 'power2.out' },
        { opacity: 0, x: -50, duration: 1, ease: 'power2.out' },
        { opacity: 0, y: -50, duration: 1, ease: 'power2.out' },
        { opacity: 0, x: 50, duration: 1, ease: 'power2.out' },
        { scale: 0, duration: 1, ease: 'back.out(1.7)' },
        // { rotation: 360, duration: 1, ease: 'power2.out' },
        { rotationX: 180, duration: 1, ease: 'elastic.out(1, 0.3)' },
        { rotationY: 180, duration: 1, ease: 'elastic.out(1, 0.3)' },
        { opacity: 0, y: 50, duration: 1, ease: 'power2.out' },
        { opacity: 0, x: -50, duration: 1, ease: 'power2.out' },
      ];
      const animation = animations[index % animations.length];
      gsap.from(element, animation);
    },
    setupObserver() {
      const elements = this.$el.querySelectorAll('.common');
      const observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const pElement = entry.target.querySelector('p');
            if (pElement) { // 确保元素存在
              const index = Array.from(elements).indexOf(entry.target);
              // pElement.classList.add('rainbow-text');
              this.animateText(pElement, index);
              observer.unobserve(entry.target);
            }
          }
        });
      }, {
        threshold: 0.5 // 当元素50%可见时触发动画
      });

      elements.forEach(el => {
        observer.observe(el);
      });
    }
  },
  mounted() {
    // 组件挂载时设置窗口高度
    this.updateWindowHeight();

    // 监听窗口大小变化事件
    // window.addEventListener('resize', this.updateWindowHeight);

    // 设置观察者
    this.setupObserver();
  },
  beforeDestroy() {
    // 组件销毁前移除事件监听
    window.removeEventListener('resize', this.updateWindowHeight);
  }
}
</script>

